import { Types } from '../actions/cart'

const initialState = {
    items: {},
    totalPrice: 0,
    totalCount: 0
}

const getTotalPrice = arr => arr.reduce((sum, obj) => obj.price + sum, 0)

// –ê–Ω–∞–ª–æ–≥ —Ñ—É–Ω–∫—Ü–∏–∏ _.get –∏–∑ –±–∏–±–ª–∏–æ—Ç–µ–∫–∏ lodash
const _get = (obj, path) => {
    const [firstKey, ...keys] = path.split('.')

    return keys.reduce((val, key) => {
        return val[key]
    }, obj[firstKey])
}

// –§—É–Ω–∫—Ü–∏—è –ø–æ–¥—Å—á–µ—Ç–∞ –∫–æ–ª–∏—á–µ—Å—Ç–≤–∞ —Ç–æ–≤–∞—Ä–æ–≤ –≤ –∫–æ—Ä–∑–∏–Ω–µ –∏–ª–∏ –∏—Ç–æ–≥–æ–≤–æ–π —Å—É–º–º—ã
const getTotalSum = (obj, path) => {
    return Object.values(obj).reduce((sum, obj) => {
        const value = _get(obj, path)
        return sum + value
    }, 0)
}

const cart = (state = initialState, action) => {
    switch (action.type) {
        case Types.ADD_PIZZA_CART: {
            const currentPizzaItems = !state.items[action.payload.id]
                ? [action.payload]
                : [...state.items[action.payload.id].items, action.payload]

            const newItems = {
                ...state.items,
                [action.payload.id]: {
                    items: currentPizzaItems,
                    totalPrice: getTotalPrice(currentPizzaItems)
                }
            }

            const totalCount = getTotalSum(newItems, 'items.length')
            const totalPrice = getTotalSum(newItems, 'totalPrice')

            return {
                ...state,
                items: newItems,
                totalCount,
                totalPrice
            }
        }

        case Types.REMOVE_CART_ITEM: {
            const newItems = {
                ...state.items
            }

            const currentTotalPrice = newItems[action.payload].totalPrice

            const currentTotalCount = newItems[action.payload].items.length

            delete newItems[action.payload]
            return {
                ...state,
                items: newItems,
                totalPrice: state.totalPrice - currentTotalPrice,
                totalCount: state.totalCount - currentTotalCount
            }
        }

        case Types.PLUS_CART_ITEM: {
            const newObjItems = [
                ...state.items[action.payload].items,
                state.items[action.payload].items[0]
            ]

            const newItems = {
                ...state.items,
                [action.payload]: {
                    items: newObjItems,
                    totalPrice: getTotalPrice(newObjItems)
                }
            }

            const totalCount = getTotalSum(newItems, 'items.length')
            const totalPrice = getTotalSum(newItems, 'totalPrice')

            return {
                ...state,
                items: newItems,
                totalCount,
                totalPrice
            }
        }

        case Types.MINUS_CART_ITEM: {
            const oldObjItems = [...state.items[action.payload].items]

            const newObjItems =
                oldObjItems.length > 1 ? oldObjItems.slice(1) : oldObjItems

            const newItems = {
                ...state.items,
                [action.payload]: {
                    items: newObjItems,
                    totalPrice: getTotalPrice(newObjItems)
                }
            }

            const totalCount = getTotalSum(newItems, 'items.length')
            const totalPrice = getTotalSum(newItems, 'totalPrice')

            return {
                ...state,
                items: newItems,
                totalCount,
                totalPrice
            }
        }

        case Types.CLEAR_CART:
            return { ...state, items: {}, totalCount: 0, totalPrice: 0 }

        default:
            return { ...state }
    }
}

export default cart
