/* eslint-disable react-hooks/exhaustive-deps */
import React from 'react'
import { useSelector, useDispatch } from 'react-redux'
import {
    Categories,
    SortPopup,
    PizzaBlock,
    PizzaLoadingBlock
} from '../components'
import PropTypes from 'prop-types'
import { setCategory, setSortBy } from '../redux/actions/filters'
import { fetchPizzas } from '../redux/actions/pizzas'
import { addPizzaToCart } from '../redux/actions/cart'

const categoryNames = [
    '–ú—è—Å–Ω—ã–µ',
    '–í–µ–≥–µ—Ç–∞—Ä–∏–∞–Ω—Å–∫–∏–µ',
    '–ì—Ä–∏–ª—å',
    '–û—Å—Ç—Ä—ã–µ',
    '–ó–∞–∫—Ä—ã—Ç—ã–µ'
]

const sortItems = [
    { name: '–ø–æ–ø—É–ª—è—Ä–Ω–æ—Å—Ç–∏', type: 'rating', order: 'desc' },
    { name: '—Ü–µ–Ω–µ', type: 'price', order: 'desc' },
    { name: '–∞–ª—Ñ–∞–≤–∏—Ç—É', type: 'name', order: 'asc' }
]

const Home = () => {
    const pizzas = useSelector(({ pizzas }) => pizzas.items)
    const isLoaded = useSelector(({ pizzas }) => pizzas.isLoaded)
    const { category, sortBy } = useSelector(({ filters }) => filters)
    const cartItems = useSelector(({ cart }) => cart.items)

    const dispatch = useDispatch()

    React.useEffect(() => {
        dispatch(fetchPizzas(sortBy, category))
    }, [sortBy, category])

    // –§—É–Ω–∫—Ü–∏—è –Ω–µ –±—É–¥–µ—Ç –ø–µ—Ä–µ—Å–æ–∑–¥–∞–≤–∞—Ç—å—Å—è –ø—Ä–∏ —Ä–µ—Ä–µ–Ω–¥–µ—Ä–µ (useCallback)
    const onSelectCategory = React.useCallback(index => {
        dispatch(setCategory(index))
    }, [])

    const onSelectSortType = React.useCallback(type => {
        dispatch(setSortBy(type))
    }, [])

    const handleAddPizza = obj => {
        dispatch(addPizzaToCart(obj))
    }

    return (
        <div className='container'>
            <div className='content__top'>
                <Categories
                    activeCategory={category}
                    onClickCategory={onSelectCategory}
                    items={categoryNames}
                />
                <SortPopup
                    onClickSortType={onSelectSortType}
                    items={sortItems}
                    activeSortType={sortBy.type}
                />
            </div>
            <h2 className='content__title'>
                {category === null ? '–í—Å–µ' : categoryNames[category]} –ø–∏—Ü—Ü—ã
            </h2>
            <div className='content__items'>
                {isLoaded
                    ? pizzas.map(pizza => (
                          <PizzaBlock
                              addedCount={
                                  cartItems[pizza.id] &&
                                  cartItems[pizza.id].items.length
                              }
                              key={pizza.id}
                              onClickAddPizza={handleAddPizza}
                              {...pizza}
                          />
                      ))
                    : Array(12)
                          .fill(0)
                          .map((_, index) => <PizzaLoadingBlock key={index} />)}
            </div>
        </div>
    )
}

export default Home

Home.propTypes = {
    pizzas: PropTypes.arrayOf(PropTypes.object).isRequired
}

Home.defaultProps = {
    pizzas: []
}
