import React, { useState, useEffect, useRef } from 'react'
import PropTypes from 'prop-types'

// React.memo —Å–ª–µ–¥–∏—Ç –∑–∞ –∏–∑–º–µ–Ω–µ–Ω–∏–µ–º –ø—Ä–æ–ø—Å–æ–≤ (–µ—Å–ª–∏ –æ–Ω–∏ –Ω–µ –∏–∑–º–µ–Ω–∏–ª–∏—Å—å, —Ç–æ –∫–æ–º–ø–æ–Ω–µ–Ω—Ç –Ω–µ —Ä–µ—Ä–µ–Ω–¥–µ—Ä–∏—Ç—Å—è)
const SortPopup = React.memo(({ items, onClickSortType, activeSortType }) => {
    const [visiblePopup, setVisiblePopup] = useState(false)
    let activeLabel = items.find(obj => obj.type === activeSortType).name

    const sortElem = useRef()

    useEffect(() => {
        document.body.addEventListener('click', handleOutsideClick)
    }, [])

    const handleOutsideClick = e => {
        const path = e.path || (e.composedPath && e.composedPath())
        if (!path.includes(sortElem.current)) {
            setVisiblePopup(false)
        }
    }

    const onSelectItem = type => {
        onClickSortType(type)
        toggleVisiblePopup()
    }

    const toggleVisiblePopup = () => {
        setVisiblePopup(!visiblePopup)
    }

    return (
        <div className='sort' ref={sortElem}>
            <div className='sort__label'>
                <svg
                    className={!visiblePopup ? 'rotate' : ''}
                    width='10'
                    height='6'
                    viewBox='0 0 10 6'
                    fill='none'
                    xmlns='http://www.w3.org/2000/svg'>
                    <path
                        d='M10 5C10 5.16927 9.93815 5.31576 9.81445 5.43945C9.69075 5.56315 9.54427 5.625 9.375 5.625H0.625C0.455729 5.625 0.309245 5.56315 0.185547 5.43945C0.061849 5.31576 0 5.16927 0 5C0 4.83073 0.061849 4.68424 0.185547 4.56055L4.56055 0.185547C4.68424 0.061849 4.83073 0 5 0C5.16927 0 5.31576 0.061849 5.43945 0.185547L9.81445 4.56055C9.93815 4.68424 10 4.83073 10 5Z'
                        fill='#2C2C2C'
                    />
                </svg>
                <b>–°–æ—Ä—Ç–∏—Ä–æ–≤–∫–∞ –ø–æ:</b>
                <span onClick={toggleVisiblePopup}>{activeLabel}</span>
            </div>
            {visiblePopup && (
                <div className='sort__popup'>
                    <ul>
                        {items &&
                            items.map((item, index) => (
                                <li
                                    key={`${item.type}_${index}`}
                                    className={
                                        activeSortType === item.type
                                            ? 'active'
                                            : ''
                                    }
                                    onClick={() => {
                                        onSelectItem(item)
                                    }}>
                                    {item.name}
                                </li>
                            ))}
                    </ul>
                </div>
            )}
        </div>
    )
})

export default SortPopup

SortPopup.propTypes = {
    items: PropTypes.arrayOf(PropTypes.object).isRequired,
    onClickSortType: PropTypes.func.isRequired,
    activeSortType: PropTypes.string.isRequired
}

SortPopup.defaultProps = {
    items: []
}
